color-estandar:active {\n  background-color: #093F75;\n  color: #FFF;\n}\n.btn-cu.btn-color-estandar:focus {\n  background-color: #0C549C;\n  color: #FFF;\n  outline: 1px dashed #000;\n}\n.mt-32 {\n  margin-top: 32px;\n}\n.mt-24 {\n  margin-top: 24px;\n}\n.mt-80 {\n  margin-top: 5em;\n}\n.mt-40 {\n  margin-top: 2.5em;\n}\n.mt-36 {\n  margin-top: 2.25em;\n}\n.mt-52 {\n  margin-top: 3.25em;\n}\n.texto-1 {\n  font-size: 1.3em;\n  color: #000000DE;\n}\n/*# sourceMappingURL=data:application/json;base64, */";